import axios from 'axios'

// 创建axios实例
const instance = axios.create({
  baseURL: process.env.VUE_APP_API_BASE_URL || 'http://8.148.21.21:3000',
  timeout: 120000 // 增加到120秒超时，适应描述生成API的长响应时间
})

// 请求拦截器
instance.interceptors.request.use(
  config => {
    // 从本地存储获取token
    const token = localStorage.getItem('token')
    
    // 如果有token，添加到请求头，并增加时间戳防止CSRF
    if (token) {
      // 添加时间戳到请求头，解决可能的签名错误问题
      config.headers['Authorization'] = `Bearer ${token}`
      config.headers['X-Timestamp'] = Date.now().toString()
      
      // 如果是店铺授权相关请求，添加额外防伪标记
      if (config.url && config.url.includes('/shops') && config.url.includes('/authorize')) {
        config.headers['X-Auth-Type'] = 'shop-auth'
      }
    }
    
    return config
  },
  error => {
    console.error('请求拦截器错误:', error)
    return Promise.reject(error)
  }
)

// 响应拦截器
instance.interceptors.response.use(
  response => {
    // 正常响应直接返回数据
    return response
  },
  error => {
    // 处理错误响应
    if (error.response) {
      // 服务器返回错误
      const { status, data } = error.response
      
      switch (status) {
        case 401:
          // 未授权，清除token并跳转到登录页
          localStorage.removeItem('token')
          localStorage.removeItem('user')
          window.location.href = '/login'
          break
        
        case 403:
          console.error('权限不足:', data.message)
          break
          
        case 404:
          console.error('请求的资源不存在:', data.message)
          break
          
        case 500:
          console.error('服务器错误:', data.message)
          break
          
        default:
          console.error('请求错误:', data.message)
      }
    } else if (error.request) {
      // 请求已发送但没有收到响应
      console.error('服务器无响应:', error.request)
    } else {
      // 请求配置错误
      console.error('请求配置错误:', error.message)
    }
    
    return Promise.reject(error)
  }
)

export default instance 