import { createApp } from 'vue'
import { createPinia } from 'pinia'
import ElementPlus from 'element-plus'
import 'element-plus/dist/index.css'
// 导入自定义主题
import './assets/styles/theme.css'
import App from './App.vue'
import router from './router'

// 导入Element Plus图标
import * as ElementPlusIconsVue from '@element-plus/icons-vue'

// 创建Vue应用实例
const app = createApp(App)

// 全局捕获并忽略ResizeObserver错误
const originalConsoleError = window.console.error
window.console.error = (...args) => {
  if (
    args.length > 0 && 
    (
      (typeof args[0] === 'string' && 
        (args[0].includes('ResizeObserver loop') || args[0].includes('ResizeObserver failed'))) ||
      (args[0] instanceof Error && 
        (args[0].message.includes('ResizeObserver loop') || args[0].message.includes('ResizeObserver failed')))
    )
  ) {
    // 忽略所有ResizeObserver循环相关错误
    return
  }
  originalConsoleError.apply(console, args)
}

// 注册所有图标
for (const [key, component] of Object.entries(ElementPlusIconsVue)) {
  app.component(key, component)
}

// 使用Pinia状态管理
app.use(createPinia())

// 使用Element Plus组件库
app.use(ElementPlus)

// 使用路由
app.use(router)

// 此处已有ResizeObserver错误处理，不需要重复代码

// 挂载应用
app.mount('#app')
